html {
  min-height: 100%;
}

body {
  margin: 0;
  padding: 0;
  background: url("/img/mario-bg.png") no-repeat;
  background-size: 100%;
  background-position: bottom;
  background-color: #95e8f2;

  min-height: 100%;
}

form {
  padding: 20px;
  margin-top: 60px;
}

form button, form h5 {
  margin: 10px 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}



